import Vue from 'vue'
import VueRouter from 'vue-router'
import Auth from '../views/Auth.vue'
import Account from '../views/Account.vue'
import Deposits from '../views/Deposits.vue'
import Dividends from '../views/Dividends.vue'
import Rules from '../views/Rules.vue'
import History from '../views/History.vue'
import CreateDeposit from '../views/CreateDeposit.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'auth',
    component: Auth
  },
  {
    path: '/account',
    name: 'account',
    component: Account
  },
  {
    path: '/deposits',
    name: 'deposits',
    component: Deposits
  },
  {
    path: '/dividends',
    name: 'dividends',
    component: Dividends
  },
  {
    path: '/rules',
    name: 'rules',
    component: Rules
  },
  {
    path: '/history',
    name: 'history',
    component: History
  },
  {
    path: '/create-deposit',
    name: 'create-deposit',
    component: CreateDeposit
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
  linkExactActiveClass: 'active'
})

router.beforeEach((to, from, next) => {
  next()
})

router.afterEach((to, from) => {
  if ((!localStorage.getItem('address') || !localStorage.getItem('publicKey')) && (to.name !== 'deposits' && to.name !== 'rules')) {
    router.push({ path: '/deposits' })
  }
})

export default router
