<template>
    <section>
        <div class="page">
            <Nav/>
            <div class="container">
                <div class="row"> 
                    <div class="col">
                        <h2>
                            Доступные депозиты
                            <!--<select v-on:change="showGame($event)" class="deposits__select" v-model="checkGame">
                                <option>Все игры</option>
                                <option>Dragon Fruit</option>
                            </select>-->
                            <select v-on:change="showCoins($event)" class="deposits__select" v-model="checkCoins">
                                <option>Все валюты</option>
                                <option v-for="(coin, index) in assetCoin" v-bind:key="index">{{coin.name}}</option>
                            </select>
                            <span class="deposits__label">
                                <span class="deposits__label__title">
                                    Текущая высота
                                </span>
                                <span class="deposits__label__annotation">
                                    {{currentHeight}}
                                </span>
                            </span>
                        </h2>
                    </div>
                </div>
                <div class="row" v-if="blockChainData.length > 0">
                    <div v-bind:class="[!item._DepositShow ? 'hide' : '']" class="col col--3 deposits__block" v-for="(item, key) in blockChainFilteredData" v-bind:key="key">
                        <div class="card__block">
                            <p class="deposits__title">
                                {{item._DepositGame}}
                            </p>
                            <p class="deposits__title deposits__title--annotation">
                                {{item._DividendPercent}}% от дохода
                            </p>
                            <img class="deposits__image" v-bind:src="require(`../assets/${item._DepositCoin.toLowerCase()}.png`)">
                            <p class="deposits__title deposits__title--deposit">
                                {{item._DepositAmount / satoshi}} {{item._DepositCoin}}
                            </p>
                            <button v-on:click="makeDeposit(key)" class="btn btn--primary deposits__button">
                                Внести депозит
                            </button>
                            <div class="deposits__hr"></div>
                            <div class="deposits__text">
                                <p class="deposits__text__title">
                                    Начало
                                </p>
                                <p class="deposits__text__coins">
                                    {{item._DepositStartTimestamp}}
                                </p>
                                <p class="deposits__text__title">
                                    Окончание
                                </p>
                                <p class="deposits__text__coins">
                                    {{item._DepositEndTimestamp}}
                                </p>
                                <p class="deposits__text__title">
                                    Срок депозита
                                </p>
                                <p class="deposits__text__coins">
                                    ~ {{item._DepositDurationDate}} дней
                                </p>
                                <p class="deposits__text__title">
                                    Начало (номер блока)
                                </p>
                                <p class="deposits__text__coins">
                                    {{item._DepositStartBlock}}
                                </p>
                                <p class="deposits__text__title">
                                    Окончание (номер блока)
                                </p>
                                <p class="deposits__text__coins">
                                    {{item._DepositEndBlock}}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>

import * as tuple from '@/tuple.js';

import Nav from '@/components/Nav.vue';
import Modal from '@/components/Modal.vue'

export default {
    name: 'deposits',
    components: {
        Nav
    },
    data() {
        return {
            checkGame: 'Все игры',
            checkCoins: 'Все валюты',
            checkAuth: true,
            currentHeight: 0,
            blockChainData: [],
            satoshi: 100000000,
            pollBlock: null,
            assetCoin: tuple.assetsNames,
        }
    },
    mounted() {
        if ((!localStorage.getItem('publicKey')) && (!localStorage.getItem('address'))) {
            this.checkAuth = false;
        };
        this.$root.$on('applyAddresAndPublic', this.addPublicAndAddress);
        this.$root.$on('removeAddresAndPublic', this.removePublicAndAddress);

        axios.get(`${tuple.urlAddressData}`)
            .then((response) => {
                this.createData(response.data)
            },
            (error) => {

            })

        axios.get(`${tuple.urlMain}/blocks/last`)
            .then((response) => {
                this.currentHeight = response.data.height;
            },
            (error) => {
                console.error(error)
            })
    },
    computed: {
        blockChainFilteredData() {
            let data = [];
            this.blockChainData.forEach((i) => {
                if (i._DepositState === "Created") {
                    data.push(i)
                }
            })
            return data;
        }
    },
    beforeDestroy () {
        clearInterval(this.pollBlock)
    },
    created () {
        this.getCurrentBlockHeight()
    },
    methods: {
        returnMonth(month) {
            return (month + 1) < 10 ? `0${month + 1}` : month + 1;
        },
        createData(arr) {
            tuple.assetsNames.forEach((i) => {
                let cacheObj = {}
                arr.forEach((a) => {
                    let name = '';
                    let regExName = new RegExp(`(${i.name})`,'g');
                    let regExValue = new RegExp(`_.*`,'g');
                    if (a.key.match(regExName)) {
                        name = a.key.match(regExValue)
                        cacheObj[name] = a.value
                    }
                })
                cacheObj["_DepositDurationDate"] = parseInt(cacheObj._DepositDurationBlock / 1440);
                let date = new Date(cacheObj["_DepositStartTimestamp"]);
                let startDate = new Date(cacheObj["_DepositStartTimestamp"]);
                let endDate = new Date(date.setDate(date.getDate() + cacheObj._DepositDurationDate));
                cacheObj["_DepositCoin"] = i.name;
                cacheObj["_DepositEndBlock"] = cacheObj._DepositStartBlock + cacheObj._DepositDurationBlock;
                cacheObj["_DepositStartTimestamp"] = `${startDate.getDate()}.${this.returnMonth(startDate.getMonth())}.${startDate.getFullYear()}`;
                cacheObj["_DepositEndTimestamp"] = `${endDate.getDate()}.${this.returnMonth(endDate.getMonth())}.${endDate.getFullYear()}`;
                cacheObj["_DepositGame"] = "Dragon Fruit";
                cacheObj["_AssetId"] = i.assetId;
                cacheObj["_DepositShow"] = true;
                this.blockChainData.push(cacheObj)
            })
        },
        async makeDeposit(key) {
            let txData = {
                fee: 500000,
                dApp: tuple.addressDapp,
                call: {
                    function: "deposit",
                    args: []
                },
                payment: [
                    {
                        amount: this.blockChainFilteredData[key]._DepositAmount,
                        assetId: this.blockChainFilteredData[key]._AssetId,
                    }
                ]
            }
            await this.signer
                .invoke(txData)
                .broadcast()
                .then((response) => {
                    for (let block in this.blockChainData) {
                        if (this.blockChainData[block]._AssetId === this.blockChainFilteredData[key]._AssetId) {
                            this.blockChainData[block]._DepositState = "Active"
                            return;
                        }
                    }
                    this.$root.$emit('showModal', {type: 'true', annotation: 'Депозит успешно внесен!'});
                },
                (error) => {
                    this.$root.$emit('showModal', {type: 'false', annotation: error.message});
                })
        },
        getCurrentBlockHeight() {
            this.pollBlock = setInterval(() => {
                axios.get(`${tuple.urlMain}/blocks/last`)
                    .then((response) => {
                    this.currentHeight = response.data.height;
                    },
                    (error) => {
                        console.error(error)
                    })
            }, 2000)
        },
        addPublicAndAddress() {
            this.checkAuth = true;
        },
        removePublicAndAddress() {
            this.checkAuth = false;
        },
        showGame(event) {
            this.checkCoins = 'Все валюты';
            let item = event.target.value;
            if (item !== 'Все игры') {
                this.blockChainData.forEach((i) => {
                    i._DepositGame.toLowerCase() === item.toLowerCase() ? i._DepositShow = true : i._DepositShow = false;
                })
            } else {
                this.blockChainData.forEach((i) => {
                    i._DepositShow = true;
                })
            }
        },
        showCoins(event) {
            this.checkGame = 'Все игры';
            let item = event.target.value;
            if (item !== 'Все валюты') {
                this.blockChainData.forEach((i) => {
                    i._DepositCoin.toLowerCase() === item.toLowerCase() ? i._DepositShow = true : i._DepositShow = false;
                })
            } else {
                this.blockChainData.forEach((i) => {
                    i._DepositShow = true;
                })
            }
        }
    },
    watch: {

    }
}
</script>

