<template>
    <section>
        <div class="page">
            <Nav/>
            <div class="container">
                <div class="row">
                    <div class="col">
                        <div class="card__block">
                            <button class="btn btn--primary btn--disabled form__btn" v-if="!verifyInput">
                                Создать заявку
                            </button>
                            <button class="btn btn--primary form__btn" v-else v-on:click="createDeposit()">
                                Создать заявку
                            </button>
                            <div class="form__block">
                                <label class="form__label">
                                    Валюта
                                </label>
                                <select v-model="inputCurrency" class="form__input">
                                    <option v-for="(item, key) in assetsName" :key="key" :value="item.name">
                                        {{item.name}}
                                    </option>
                                </select>
                            </div>
                            <div class="form__block">
                                <label class="form__label">
                                    Количество (в монетах)
                                </label>
                                <input v-model="inputAmount" class="form__input" type="number">
                            </div>
                            <div class="form__block">
                                <label class="form__label">
                                    Длительность (кол-во дней)
                                </label>
                                <input v-model="inputDuration" class="form__input" type="number">
                            </div>
                            <div class="form__block">
                                <label class="form__label">
                                    Процент дивидедов
                                </label>
                                <input v-model="inputDividendPercent" class="form__input" type="number">
                            </div>
                            <div class="form__block">
                                <label class="form__label">
                                    Timestamp начала
                                </label>
                                <input v-model="inputStartTimestamp" class="form__input" type="number">
                            </div>
                            <div class="form__block">
                                <label class="form__label">
                                    Начало (высота блокчейна)
                                </label>
                                <input v-model="inputStartHeight" class="form__input" type="number">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>

import * as tuple from '@/tuple.js';

import Nav from '@/components/Nav.vue';

export default {
    name: 'create-deposit',
    components: {
        Nav
    },
    data() {
        return {
            assetsName: tuple.assetsNames,
            dappAddress: tuple.addressDapp,
            satoshi: 100000000,
            stableSatoshi: 1000000,
            dayInBlocks: 1440,
            inputCurrency: 'WAVES',
            inputAmount: 0,
            inputDuration: 0,
            inputDividendPercent: 0,
            inputStartTimestamp: 0,
            inputStartHeight: 0,
        }
    },
    computed: {
        verifyInput() {
            if (
                this.inputAmount > 0 &&
                this.inputDuration > 0 &&
                this.inputDividendPercent > 0 &&
                this.inputStartTimestamp > 0 &&
                this.inputStartHeight > 0
            ) {
                return true;
            } else {
                return false;
            }
        }
    },
    mounted() {

    },
    methods: {
        async createDeposit() {
            let currency = this.inputCurrency;
            let amount = null;
            if (currency === 'USDN' || currency === 'USDT') {
                amount = this.inputAmount * this.stableSatoshi;
            } else {
                amount = this.inputAmount * this.satoshi;
            }
            let durationBlocks = this.inputDuration * this.dayInBlocks;

            let txData = {
                fee: 500000,
                dApp: this.dappAddress,
                call: {
                    function: "createDeposit",
                    args: [
                        {
                            type: "string", value: currency
                        },
                        {
                            type: "integer", value: amount
                        },
                        {
                            type: "integer", value: this.inputDividendPercent
                        },
                        {
                            type: "integer", value: this.inputStartHeight
                        },
                        {
                            type: "integer", value: durationBlocks
                        },
                        {
                            type: "integer", value: this.inputStartTimestamp
                        }
                    ]
                },
                payment: []
            }
            await this.signer
                .invoke(txData)
                .broadcast()
                .then((response) => {
                    console.log(response)
                    this.inputCurrency =  'WAVES';
                    this.inputAmount =  0;
                    this.inputDuration =  0;
                    this.inputDividendPercent =  0;
                    this.inputStartTimestamp =  0;
                    this.inputStartHeight =  0;
                },
                (error) => {
                    alert(JSON.stringify(error))
                })
        }
    },
    watch: {

    }
}
</script>

