<script>

import * as tuple from '@/tuple.js';

import Nav from '@/components/Nav.vue';

export default {
  name: 'account',
  components: {
    Nav,
  },
  data() {
    return {
      publicKey: '',
      address: '',
      blockChainData: [],
      satoshi: 100000000,
      balanceData: [],
      history: []
    }
  },
  mounted() {
    this.publicKey = localStorage.getItem('publicKey');
    this.address = localStorage.getItem('address');
    axios.get(`${tuple.urlAddressData}`)
      .then((response) => {
          this.createData(response.data)
       },
       (error) => {
        console.error(error)
      })
  },
  computed: {
      blockChainFilteredData() {
          let data = [];
          this.blockChainData.forEach((i) => {
              if (i._DepositState !== "Created" && i._DepositOwner === this.publicKey) {
                //console.log(`${i._DividendBalance * this.satoshi}, ${i._DividendWithdrawn}, ${i._DividendPercent}`)
                i._TotalBalance = ((i._DividendBalance + i._DividendWithdrawn) * i._DividendPercent) / 100;
                i._TotalBalanceDeposit = i._TotalBalance - i._DividendWithdrawn;
                data.push(i)
                return
              }
          })
          return data;
      }
  },
  methods: {
    async withdrawDividends(_AssetId, key) {
      let txData = {
        fee: 500000,
        dApp: tuple.addressDapp,
        call:{
            function: "withdrawDividends",
            args: [
              {
                type: 'string',
                value: _AssetId
              }
            ]
        },
        payment: []
      }
      await this.signer
          .invoke(txData)
          .broadcast()
          .then((response) => {
            for (let block in this.blockChainData) {
              if (this.blockChainData[block]._AssetId === this.blockChainFilteredData[key]._AssetId) {
                this.blockChainData
                this.blockChainData[block]._DepositState = "Active"
                return;
              }
            }
            this.$root.$emit('showModal', {type: 'true', annotation: 'Дивиденды успешно выведены!'});
          },
          (error) => {
              this.$root.$emit('showModal', {type: 'false', annotation: error.message});
          })
    },
    async withdrawDeposit(_AssetId, key) {
      let txData = {
        fee: 500000,
        dApp: tuple.gameAddress,
        call:{
            function: "withdrawDeposit",
            args: [
              {
                type: 'string',
                value: _AssetId
              }
            ]
        },
        payment: []
      }
      await this.signer
          .invoke(txData)
          .broadcast()
          .then((response) => {                
            for (let block in this.blockChainData) {
                if (this.blockChainData[block]._AssetId === this.blockChainFilteredData[key]._AssetId) {
                    this.blockChainData[block]._DepositState = "Active"
                    return;
                }
            }
            this.$root.$emit('showModal', {type: 'true', annotation: 'Депозит успешно выведен!'});
          },
          (error) => {
              this.$root.$emit('showModal', {type: 'false', annotation: error.message});
          })
    },
    returnMonth(month) {
      return (month + 1) < 10 ? `0${month + 1}` : month + 1;
    },
    createData(arr) {
      tuple.assetsNames.forEach((i) => {
        let cacheObj = {}
        arr.forEach((a) => {
          let name = '';
          let regExName = new RegExp(`(${i.name})`,'g');
          let regExValue = new RegExp(`_.*`,'g');
          if (a.key.match(regExName)) {
            name = a.key.match(regExValue)
            cacheObj[name] = a.value
          }
        })
        cacheObj["_DepositDurationDate"] = parseInt(cacheObj._DepositDurationBlock / 1440);
        let date = new Date(cacheObj["_DepositStartTimestamp"]);
        let startDate = new Date(cacheObj["_DepositStartTimestamp"]);
        let endDate = new Date(date.setDate(date.getDate() + cacheObj._DepositDurationDate));
        let end = (startDate - endDate) / (60 * 60 * 24 * 1000);
        cacheObj["_DepositCoin"] = i.name;
        cacheObj["_DepositEndBlock"] = cacheObj._DepositStartBlock + cacheObj._DepositDurationBlock;
        cacheObj["_DepositStartTimestamp"] = `${startDate.getDate()}.${this.returnMonth(startDate.getMonth())}.${startDate.getFullYear()}`;
        cacheObj["_DepositEndTimestamp"] = `${endDate.getDate()}.${this.returnMonth(endDate.getMonth())}.${endDate.getFullYear()}`;
        cacheObj["_DepositEnd"] = `${end}`;
        cacheObj["_DepositGame"] = "Dragon Fruit";
        cacheObj["_AssetId"] = i.assetId;
        cacheObj["_DepositShow"] = true;
        let urlGet = i.assetId === 'WAVES' ? `${tuple.urlMain}/addresses/balance/${tuple.addressDapp}` : `${tuple.urlMain}/assets/balance/${tuple.addressDapp}/${i.assetId}`;
        axios
          .get(urlGet)
          .then((response) => {
            cacheObj["_DividendBalance"] = response.data.balance;
            this.blockChainData.push(cacheObj)
          },
          (error) => {
            this.$root.$emit('showModal', {type: 'false', annotation: error.message});
          })
      })
    },
  },
  watch: {

  }
}
</script>

