<template>
  <section>
    <div class="page">
      <Nav/>
      <div class="container">
        <div class="row">
          <div class="col col">
            <h2>
              Дивидендные начисления
            </h2>
            <div class="card__block card__block--noOffset card__block--table">
              <div class="table">
                <div class="table__item">
                  <div class="table__col table__name">
                    Dragon Fruit
                  </div>
                </div>
                <div class="table__item">
                  <div class="table__col table__coin">
                    Валюта
                  </div>
                  <div class="table__col table__cash">
                    Общий доход
                  </div>
                  <div class="table__col table__size">
                    Размер депозита
                  </div>
                  <div class="table__col table__give">
                    Начислено дивидендов
                  </div>
                  <div class="table__col table__percent">
                    Процент начислений
                  </div>
                  <div class="table__col table__year">
                    Годовая доходность
                  </div>
                </div>
                <div class="table__item" v-for="(item, index) in blockChainFilteredData" v-bind:key="index">
                  <div class="table__col table__coin">
                    <img class="deposits__image" v-bind:src="require(`../assets/${item._DepositCoin.toLowerCase()}.png`)">
                  </div>
                  <div class="table__col table__cash">
                    {{item._DividendBalanceDisplay}} {{item._DepositCoin}}
                  </div>
                  <div class="table__col table__size">
                    {{item._DepositAmountDisplay}} {{item._DepositCoin}}
                  </div>
                  <div class="table__col table__give">
                    {{item._TotalBalanceDisplay}} {{item._DepositCoin}}
                  </div>
                  <div class="table__col table__percent">
                    {{item._DividendPercent}}%
                  </div>
                  <div class="table__col table__year">
                    {{item._Yearly}}%
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>

import * as tuple from '@/tuple.js';

import Nav from '@/components/Nav.vue';

export default {
  name: 'dividends',
  components: {
    Nav
  },
  data() {
    return {
      address: '',
      publicKey: '',
      blockChainData: [],
      satoshi: 100000000,
      stableSatoshi: 1000000,
      currentHeight: null
    }
  },
  computed: {
    blockChainFilteredData() {
      let data = [];
      this.blockChainData.forEach((i) => {
        i._DividendWithdrawn ? i._DividendWithdrawn = i._DividendWithdrawn : i._DividendWithdrawn = 0;
        i._TotalBalanceDisplay = (i._DepositCoin === 'USDT' || i._DepositCoin === 'USDN') ? ((((i._DividendBalance + i._DividendWithdrawn) * i._DividendPercent) / 100) / this.stableSatoshi).toFixed(2).replace('.00', '') : ((((i._DividendBalance + i._DividendWithdrawn) * i._DividendPercent) / 100) / this.satoshi).toFixed(2).replace('.00', '');
        i._DividendBalanceDisplay = (i._DepositCoin === 'USDT' || i._DepositCoin === 'USDN') ? (i._DividendBalance / this.stableSatoshi).toFixed(2).replace('.00', '') : (i._DividendBalance / this.satoshi).toFixed(2).replace('.00', '');
        i._DepositAmountDisplay = (i._DepositCoin === 'USDT' || i._DepositCoin === 'USDN') ? (i._DepositAmount / this.stableSatoshi).toFixed(2).replace('.00', '') : (i._DepositAmount / this.satoshi).toFixed(2).replace('.00', '');
        i._Yearly = ((((i._TotalBalanceDisplay / i._DepositAmountDisplay) / ((this.currentHeight - i._DepositStartBlock) / 1440)) * 365) * 100).toFixed(2).replace('.00', '') ;
        if (i._DepositDurationDate) {
          data.push(i)
        }
      })
      return data;
    }
  },
  mounted() {
    this.publicKey = localStorage.getItem('publicKey');
    this.address = localStorage.getItem('address');
    axios.get(`${tuple.urlMain}/blocks/last`)
      .then((response) => {
        this.currentHeight = response.data.height;
      },      
      (error) => {
        console.error(error)
      })
    axios.get(`${tuple.urlAddressData}`)
      .then((response) => {
          this.createData(response.data)
       },
       (error) => {
        console.error(error)
      })
  },
  methods: {
    createData(arr) {
      tuple.assetsNames.forEach((i) => {
        let cacheObj = {}
        arr.forEach((a) => {
          let name = '';
          let regExName = new RegExp(`(${i.name})`,'g');
          let regExValue = new RegExp(`_.*`,'g');
          if (a.key.match(regExName)) {
            name = a.key.match(regExValue)
            cacheObj[name] = a.value
          }
        })
        cacheObj["_DepositDurationDate"] = parseInt(cacheObj._DepositDurationBlock / 1440);
        let startDate = new Date(cacheObj["_DepositStartTimestamp"]);
        let endDate = new Date(startDate.setDate(startDate.getDate() + cacheObj._DepositDurationDate));
        let end = (startDate - endDate) / (60 * 60 * 24 * 1000);
        cacheObj["_DepositCoin"] = i.name;
        cacheObj["_DepositEndBlock"] = cacheObj._DepositStartBlock + cacheObj._DepositDurationBlock;
        cacheObj["_DepositStartTimestamp"] = `${startDate.getDate()}.${startDate.getMonth() + 1}.${startDate.getFullYear()}`;
        cacheObj["_DepositEndTimestamp"] = `${endDate.getDate()}.${endDate.getMonth() + 1}.${endDate.getFullYear()}`;
        cacheObj["_DepositEnd"] = `${end}`;
        cacheObj["_DepositGame"] = "Dragon Fruit";
        cacheObj["_AssetId"] = i.assetId;
        cacheObj["_DepositShow"] = true;
        let urlGet = i.assetId === 'WAVES' ? `${tuple.urlMain}/addresses/balance/${tuple.addressDapp}` : `${tuple.urlMain}/assets/balance/${tuple.addressDapp}/${i.assetId}`;
        axios
          .get(urlGet)
          .then((response) => {
            cacheObj["_DividendBalance"] = response.data.balance;
            this.blockChainData.push(cacheObj)
          },
          (error) => {
            this.$root.$emit('showModal', {type: 'false', annotation: error.message});
          })
      })
    },
  },
  watch: {

  }
}
</script>

