<template>
    <div :class="[modalShow ? 'active' : '']" class="modal">
        <div class="modal__backdrop" @click="closeModal()"></div>
        <div class="modal__container">
            <div v-if="modalType" class="modal__content modal__content--success">
                <div class="modal__content__svg">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1" stroke-linecap="round" stroke-linejoin="round" class="feather feather-smile"><circle cx="12" cy="12" r="10"></circle><path d="M8 14s1.5 2 4 2 4-2 4-2"></path><line x1="9" y1="9" x2="9.01" y2="9"></line><line x1="15" y1="9" x2="15.01" y2="9"></line></svg>
                </div>
                <p class="modal__content__title">
                   Готово!
                </p>
                <p class="modal__content__annotation">
                    {{modalAnnotation}}
                </p>
            </div>
            <div v-else class="modal__content modal__content--error">
                <div class="modal__content__svg">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1" stroke-linecap="round" stroke-linejoin="round" class="feather feather-frown"><circle cx="12" cy="12" r="10"></circle><path d="M16 16s-1.5-2-4-2-4 2-4 2"></path><line x1="9" y1="9" x2="9.01" y2="9"></line><line x1="15" y1="9" x2="15.01" y2="9"></line></svg>
                </div>
                <p class="modal__content__title">
                   Ошибка
                </p>
                <p class="modal__content__annotation">
                    {{modalAnnotation}}
                </p>
            </div>
            <button class="modal__close" @click="closeModal()">
                Закрыть
            </button>
        </div>
    </div>    
</template>

<script>

// type: Bolean
// annotation: String
// Use this.$root.$emit('showModal', {type: '<type>', annotation: '<annotation>'})

export default {
    data() {
        return {
            modalShow: false,
            modalType: true,
            modalAnnotation: '',
        }
    },
    mounted() {
        this.$root.$on('showModal', this.showModal);
    },
    methods: {
        showModal(data) {
            this.modalShow = true;
            this.modalType = data.type === 'true' ? true : false;
            this.modalAnnotation = data.annotation;
        },
        closeModal() {
            this.modalShow = false;
        }
    }
}
</script>

