<template>
  <div class="nav">
    <div class="container">
      <div class="row">
        <div class="col">
          <div class="nav__mobile">
            <p class="nav__mobile__logo">FDP</p>
            <button v-on:click="openMobileMenu(true)" v-if="!mobileMenu" class="nav__mobile__button"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-menu"><line x1="3" y1="12" x2="21" y2="12"></line><line x1="3" y1="6" x2="21" y2="6"></line><line x1="3" y1="18" x2="21" y2="18"></line></svg></button>
            <button v-on:click="openMobileMenu(false)" v-else class="nav__mobile__button"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-x"><line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line></svg></button>
          </div>
          <div class="nav__menu" :class="[mobileMenu ? 'active' : '']">
            <div class="nav__overflow">
              <router-link v-if="(address || publicKey)" class="nav__item" :to="{ name: 'account'}">
                Мой аккаунт
              </router-link>
              <router-link class="nav__item" :to="{ name: 'deposits'}">
                Доступные депозиты
              </router-link>
              <router-link v-if="(address || publicKey)" class="nav__item" :to="{ name: 'dividends'}">
                Дивидендные начисления
              </router-link>
              <router-link class="nav__item" :to="{ name: 'rules'}">
                О программе
              </router-link>
              <router-link v-if="verifyPublicKey" class="nav__item" :to="{ name: 'create-deposit'}">
                Создать депозит
              </router-link>
              <!--<router-link v-if="(address || publicKey)" class="nav__item" :to="{ name: 'history'}">
                История депозитов
              </router-link>-->
              <button v-if="(address || publicKey)"  class="nav__right" v-on:click="logOut">
                Выход
              </button>
              <button v-else  class="nav__item nav__right" v-on:click="authorisation">
                Войти
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Nav',
  data() {
    return {
      installKeeper: true,
      address: '',
      publicKey: '',
      mobileMenu: false,
    }
  },
  mounted() {
    if (localStorage.getItem('publicKey') && localStorage.getItem('address')) {
      this.addPublicAndAddress();
    }
    this.$root.$on('applyAddresAndPublic', this.addPublicAndAddress)
    this.$root.$on('handlerBodyClick', this.handlerClick)
  },
  computed: {
    verifyPublicKey() {
      if (this.publicKey === 'BuKYJ7VTkqha8jon5UdXdXMvbuYs1PL89mFJhmfrhsm9') {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    async logOut() {
      await this.signer
        .logout()
        .then((response) => {
          localStorage.removeItem('publicKey');
          localStorage.removeItem('address');
          this.publicKey = '';
          this.address = '';
          if ((this.$route.name !== 'deposits' && this.$route.name !== 'rules')) {
            this.$router.push({ path: '/deposits' })
          }
          this.$root.$emit('removeAddresAndPublic', []);
        },
        (error) => {
          alert(JSON.stringify(error))
        });
    },
    authorisation() {
      this.$root.$emit('keeper', []);
      this.mobileMenu = false;
    },
    addPublicAndAddress() {
      this.publicKey = localStorage.getItem('publicKey');
      this.address = localStorage.getItem('address');
    },
    openMobileMenu(state) {
      !state ? this.mobileMenu = false : this.mobileMenu = true;
    },
    handlerClick(event) {
      let click = event[0];
      let check = true;
      for (let i = 0;i < click.path.length; i = i + 1) {
        if (click.path[i].className === 'nav' && this.mobileMenu) {
          check = false;
          return false;
        }
      }
      if (check) {
        this.mobileMenu = false;
      }
    }
  }
}
</script>


