<template>
  <div v-on:click="bodyClick">
    <transition name="fade">
      <router-view/>
    </transition>
    <transition name="fade">
      <div v-show="preload" class="preloader">
        <div class="preloader__container">
          <div class="preloader__logo">
            <svg width="165" height="215" viewBox="0 0 165 215" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M132 33.2L165 0.200012V214.7H0L33 181.7H132V33.2Z" fill="#2F80ED" fill-opacity="0.5"/>
              <path d="M165 0.200012H0V214.7L33 181.7V123.95H66L99 90.95H33V33.2H132L165 0.200012Z" fill="#2F80ED"/>
            </svg>
          </div>
          <p class="preloader__title"><b>Fortune</b> Pool Dividend Program</p>
        </div>
        <p class="preloader__powered"><span class="gray">Powered on</span> <img v-bind:src="require(`./assets/waves-logo.svg`)"> Platform</p>
      </div>
    </transition>
    <Modal />
  </div>
</template>

<script>

  import * as tuple from '@/tuple.js';
  import Modal from './components/Modal.vue'

  export default {
    data() {
      return {
        preload: true
      }
    },
    components: {
      Modal
    },
    mounted() {
      window.addEventListener('load', () => {
        this.preload = false;
      });
      this.$root.$on('keeper', this.authKeeper)
    },
    methods: {
      async authKeeper() {
        await this.signer
          .login(tuple.authData)
          .then((response) => {
            localStorage.setItem('publicKey', response.publicKey)
            localStorage.setItem('address', response.address)
            this.$root.$emit('applyAddresAndPublic', []);
          },
          (error) => {
            console.error(error)
          });
      },
      bodyClick(event) {
        this.$root.$emit('handlerBodyClick', [event]);
      },
    },
    watch: {

    }
  }
</script>

